import React, { useState } from "react";
import { withStyles } from "@material-ui/core/styles";
import {
  Grid,
  Card,
  CardActions,
  CardActionArea,
  CardContent,
  CardMedia,
  Typography,
  Box,
  Icon,
  SvgIcon,
  Button,
} from "@material-ui/core";

const styles = {
  card: { backgroundColor: "#F9564F !important" },
  cardcontent: {
    "&:last-child": {
      paddingBottom: 16,
    },
  },
};

function TitleCard(props) {
  const [hovering, setHovering] = useState(false);
  return (
    <Grid container justify="center" spacing={3}>
      <Grid item lg={8} md={8} sm={12} xs={12}>
        <Card
          className={props.classes.card}
          elevation={hovering ? 24 : 1}
          onMouseOver={() => setHovering(true)}
          onMouseOut={() => setHovering(false)}
          onClick={() => (window.location.href = "/questions")}
        >
          <CardActionArea>
            <CardContent className={props.classes.cardcontent}>
              <Grid container spacing={3} alignItems="center">
                <Grid item xs={11}>
                  <Typography gutterBottom variant="h5" component="h2">
                    As you learn about the Flint water crisis, don't forget to
                    keep track of your thoughts. What do you see, think, and
                    wonder?
                  </Typography>
                </Grid>
                <Grid item xs={1}>
                  <Icon fontSize="large">arrow_forward</Icon>
                </Grid>
              </Grid>
            </CardContent>
          </CardActionArea>
        </Card>
      </Grid>
    </Grid>
  );
}

export default withStyles(styles)(TitleCard);
